.App{
    background-color: #111;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    overflow: hidden;
    margin: 0;
}

.container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center ;
    max-width: 400px;
}
.square{
    border-radius: 25px;
    width: 16px;
    height: 16px;
    background-color: #1d1d1d;
    box-shadow: 0 0 2px #000;
    transition: 2s ease;
}
.square:hover{
    transition-duration: 0s;
}